.add_nft {
  padding: 50px 0 0 0;
  &__left {
    h5 {
      color: $white_pure;
      margin-bottom: 20px;
    }
    .preview_card {
      border: 1px solid $white_dull;
      padding: 30px;
      border-radius: 10px;

      box-shadow: $box_shadow_one;
      .prevImg {
        width: 100%;
        border-radius: 10px;
        overflow: hidden;
        height: 320px;
        border: 1px solid $white_dull;
        // background-color: transparent;
        img {
          @include image(cover);
          border-radius: 10px;
        }
      }
      h6 {
        margin: 10px 0;
      }
    }
  }
  &__right {
    margin-top: 20px;
    padding-left: 100px;
    @include mQ($lg) {
      padding-left: 0px;
    }
    label {
      color: $white_pure;
    }
    input,
    select {
      padding: 15px;
      font-size: 15px;
      background-color: black !important;
      border-radius: 5px !important;
      border: 0;
      color: $white_pure !important;
    }
    span {
      color: #dc3545 !important;
      font-size: 11px;
    }
  }
  @include mQ($sm) {
    &__left {
      .preview_card {
        padding: 10px;
      }
    }
  }
}

.fileName {
  font-size: 11px;
}
.fileNameError {
  font-size: 11px;
  color: #dc3545 !important;
}
