.profile_section {
  @include displayCenter(center, center);
  flex-direction: column;
  position: relative;
  margin: 50px 0 30px 0;

  .profile_image {
    width: 150px;
    height: 150px;
    border-radius: 50%;
    background: $secondary_theme;
    @include displayCenter(center, center);
    @include neonEffectModal(50%, $neon_background_seven);
    img {
      @include image(cover);
      border-radius: 50%;
    }
    @include mQ($sm) {
      width: 100px;
      height: 100px;
    }
  }
  .upload_icon {
    position: absolute;
    bottom: -15px;
    cursor: pointer;
    width: 30px;
    height: 30px;
    border-radius: 50%;
    background: $secondary_theme;
    @include displayCenter(center, center);
    svg {
      font-size: 20px;
    }
  }
}


.userNAme {
  margin: -15px 0 0 0;
  @include displayCenter(center, center);
  input {
    background-color: transparent;
    max-width: 400px;
    color: $white_pure !important;
    font-size: 18px;
    text-align: center;
    &:focus {
      background-color: transparent;
    }
  }
}
._user__id {
  margin: 0px 0;
  position: relative;
  @include displayCenter(center, center);

  p {
    padding: 10px 25px;
    position: relative;
    cursor: pointer;
    svg {
      margin-left: 10px;
    }
    background: $secondary_theme;
    @include neonEffectModal(0, $neon_background_three);
  }
  &:hover {
    .toolt {
      visibility: visible;
    }
  }
  @include mQ($sm) {
    p {
      @include userAddress(200px);
    }
  }
}
