.faq {
  padding: 50px 0 150px 0;
  .faq__container {
    margin-top: 50px;
    @include displayCenter(center, center);
    flex-direction: column;
    .faq_containet {
      max-width: 800px;
      padding: 15px;
      background: $secondary_theme;
      @include neonEffectFour(5px);
      border-radius: 5px;
      margin: auto;
      margin-bottom: 30px;
      cursor: pointer;
    }
    .faq_item {
      @include displayCenter(flex-start, space-between);
      div {
        h6 {
          color: $white_pure;
        }
        svg {
          font-size: 23px;
        }
      }
    }
  }

  @include mQ($sm) {
    padding: 50px 0;
  }
}
